import { exec } from 'child_process';
import { promisify } from 'util';
import fs from 'fs/promises';
import path from 'path';

const execAsync = promisify(exec);

const MAX_FILE_SIZE = 25 * 1024 * 1024; // 25MB

/**
 * Compress audio file to meet OpenAI Whisper API size requirements
 * Reduces sample rate to 16kHz (sufficient for speech) and uses higher compression
 */
export async function compressAudioIfNeeded(
  inputPath: string,
  filename: string
): Promise<{ path: string; compressed: boolean; originalSize: number; finalSize: number }> {
  try {
    // Check file size
    const stats = await fs.stat(inputPath);
    const originalSize = stats.size;

    // If file is under 25MB, no compression needed
    if (originalSize <= MAX_FILE_SIZE) {
      return {
        path: inputPath,
        compressed: false,
        originalSize,
        finalSize: originalSize,
      };
    }

    console.log(`File ${filename} is ${(originalSize / 1024 / 1024).toFixed(2)}MB, compressing...`);

    // Create temporary output path
    const outputPath = inputPath.replace('.wav', '_compressed.wav');

    // FFmpeg command to compress:
    // - Convert to mono (reduces size by ~50% if stereo)
    // - Sample rate: 16kHz (sufficient for speech recognition, down from typically 44.1kHz or 48kHz)
    // - Codec: PCM 16-bit (good quality, better compression than 24/32-bit)
    const ffmpegCommand = [
      'ffmpeg',
      '-i', `"${inputPath}"`,
      '-ar', '16000',           // Sample rate: 16kHz
      '-ac', '1',               // Mono audio
      '-sample_fmt', 's16',     // 16-bit PCM
      '-y',                     // Overwrite output file
      `"${outputPath}"`
    ].join(' ');

    await execAsync(ffmpegCommand);

    // Check compressed file size
    const compressedStats = await fs.stat(outputPath);
    const finalSize = compressedStats.size;

    console.log(
      `Compression complete: ${(originalSize / 1024 / 1024).toFixed(2)}MB → ${(finalSize / 1024 / 1024).toFixed(2)}MB ` +
      `(${((1 - finalSize / originalSize) * 100).toFixed(1)}% reduction)`
    );

    // If still too large, try more aggressive compression
    if (finalSize > MAX_FILE_SIZE) {
      console.log('File still too large, applying additional compression...');

      const outputPath2 = inputPath.replace('.wav', '_compressed2.wav');

      // More aggressive: 8kHz sample rate (minimum for acceptable speech quality)
      const ffmpegCommand2 = [
        'ffmpeg',
        '-i', `"${outputPath}"`,
        '-ar', '8000',           // Lower sample rate: 8kHz
        '-ac', '1',              // Mono audio
        '-sample_fmt', 's16',    // 16-bit PCM
        '-y',
        `"${outputPath2}"`
      ].join(' ');

      await execAsync(ffmpegCommand2);

      // Clean up first compressed file
      await fs.unlink(outputPath).catch(() => {});

      const finalStats = await fs.stat(outputPath2);
      const finalSize2 = finalStats.size;

      console.log(
        `Aggressive compression complete: ${(originalSize / 1024 / 1024).toFixed(2)}MB → ${(finalSize2 / 1024 / 1024).toFixed(2)}MB`
      );

      // Clean up original file
      await fs.unlink(inputPath).catch(() => {});

      return {
        path: outputPath2,
        compressed: true,
        originalSize,
        finalSize: finalSize2,
      };
    }

    // Clean up original file
    await fs.unlink(inputPath).catch(() => {});

    return {
      path: outputPath,
      compressed: true,
      originalSize,
      finalSize,
    };
  } catch (error) {
    console.error('Audio compression error:', error);
    throw new Error(`Failed to compress audio: ${(error as Error).message}`);
  }
}

/**
 * Format bytes to human-readable size
 */
export function formatBytes(bytes: number): string {
  if (bytes === 0) return '0 Bytes';
  const k = 1024;
  const sizes = ['Bytes', 'KB', 'MB', 'GB'];
  const i = Math.floor(Math.log(bytes) / Math.log(k));
  return Math.round(bytes / Math.pow(k, i) * 100) / 100 + ' ' + sizes[i];
}
